import { Component, EventEmitter, Input, OnInit, Output, ViewChild, } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal, NgbModalConfig, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import * as _ from 'underscore';
import { TreasuryService } from '../../treasury/petty-cash-treasury/services/treasury.service';
import { GeneralService } from '../../../../services/general.service';
import { HeaderService } from '../../../../services/header.service';
import { ExpenseDetailsComponent } from './components/expense-details/expense-details.component';
import { SharedService } from '../../../../services/shared.service';
import { ConfirmModalComponent } from '../../../../shared/components/confirm-modal/confirm-modal.component';
import { ModalMessageComponent } from '../../../../shared/components/modal-message/modal-message.component';
import { RegisterSetupService } from '../../admin/registers/register-setup.service';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-petty-cash-approval',
  templateUrl: './petty-cash-approval.component.html',
  styleUrls: ['./petty-cash-approval.component.scss'],
  providers: [
    NgbActiveModal,
  ]
})
export class PettyCashApprovalComponent {
  @Input() registerId: number;
  expenses: any[] = [];
  totalRecords!: number;
  searchText: string = '';
  status: string = '';
  sort: number = 1;
  pageNo: number = 1;
  checkedItems;
  action;
  reasonModelRef: NgbModalRef;
  @Output() sendtoLoadData = new EventEmitter();
  appForm: FormGroup;
  registerDetails;
  constructor(
    private _registerService: RegisterSetupService,
    private treasuryService: TreasuryService,
    public generalService: GeneralService,
    public headerService: HeaderService,
    private modalService: NgbModal,
    private _activeModal: NgbActiveModal,
    private _sharedService: SharedService,
    private router: Router,
    config: NgbModalConfig,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private _spinnerService: NgxSpinnerService,
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    this.appForm = this.fb.group({
      rejectReason: ['', [Validators.required]]
    })
    this.getRegisterDetailsV2();
  }
  getRegisterDetailsV2() {
    //&RegisterId=${ this._sharedService.getRegister.registerObj.id }
    this._spinnerService.show();
    let registerId = this._sharedService.getRegister?.registerObj?.id;
    this._registerService.getRegisterDetailsV2(registerId).subscribe((response: any) => {
      this.registerDetails = response.data;
      this.GetMainData();
    });
  }
  GetMainData() {
    let type = this.registerDetails.pettyCashApprovalType;
    let registerId = this._sharedService.getRegister.registerObj.id;
    let userId = this._sharedService.getUserId;
    let params = `?Status=${2011}&Sort=${this.sort}&PageNo=${this.pageNo - 1}&PageSize=6`;
    if (type == 'Cost Center' || !type)
      params += `&approvalId=${userId}`
    else if (type == 'Direct Manager') {
      if (this.registerDetails.pettyCashManagerID == userId)
        params += `&registerId=${registerId}`;
      else {
        this._spinnerService.hide();
        return;
      }
    }
    this.treasuryService.getPettyCashPendingManagerApprovalExpenses(params).subscribe((response: any) => {
      if (response) {
        this.expenses = response.data.map((x: any) => {
          return { ...x, checked: false }
        });
        this.totalRecords = response.info.totalRecordCount;
      }
    }).add(() => {
      this._spinnerService.hide();
    })
  }
  handleCheckBoxChange() {
    this.checkedItems = this.expenses.filter((x: any) => x.checked);
  }
  handleCheckAll(event) {
    let changedArr = [];
    if (event.target.checked) {
      changedArr = this.expenses.map(x => {
        return { ...x, checked: true }
      })
      this.expenses = changedArr
      this.checkedItems = changedArr;
    }
    else {
      changedArr = this.expenses.map(x => {
        return { ...x, checked: false }
      })
      this.expenses = changedArr;
      this.checkedItems = [];
    }
  }
  Viewdetails(item) {
    this.router.navigateByUrl('/petty-cash/request-details/1');
    this.headerService.setTitle(
      'Petty Cash Monitoring > Petty Cash Replenishment Request > View Details'
    );
  }
  viewExpense() {
    this.headerService.setTitle('petty-cash > View role');
  }
  confirmModal(action, content) {
    this.appForm.reset();
    this.action = action;
    const modelRef = this.modalService.open(ConfirmModalComponent);
    modelRef.componentInstance.data = { body: `Are you sure you want to ${action} expense` };
    modelRef.componentInstance.eventData.subscribe(value => {
      if (value) {
        this.handleMultipleExpense(content);
      }
    })
  }
  handleMultipleExpense(content) {
    this.save();
    if (this.action !== 'approve') {
      this.reasonModelRef = this.modalService.open(content, { centered: true, size: 'md' });
    }
  }
  save() {
    let ids = this.checkedItems.map(x => x.expenseRecordId).toString();
    const formData = new FormData();
    formData.append('ExpenseRecordId', ids);
    if (this.action == 'approve') {
      this.treasuryService.approverApproveExpense(formData).subscribe(response => {
        console.log(response);
        if (response.data) {
          this.checkedItems = [];
          this.GetMainData();
          this.toastr.success('Expense approved successfully!', 'Success')
        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      })
    }
    else if (this.action == 'revise') {
      if (!this.appForm.valid)
        return
      formData.append('Notes', this.appForm.get('rejectReason').value);
      this.treasuryService.requestReviseExpense(formData).subscribe(response => {
        if (response.data) {
          this.checkedItems = [];
          this.GetMainData();
          this.toastr.success('Expense revised successfully!', 'Success');
          this.reasonModelRef.dismiss();
        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      })
    }
    else {
      if (this.appForm.valid) {
        formData.append('RejectionReason', this.appForm.get('rejectReason').value);
        this.treasuryService.rejectMultipleExpense(formData).subscribe(response => {
          console.log(response);
          if (response.data) {
            this.checkedItems = [];
            this.GetMainData();
            this.toastr.success('Expense rejected successfully!', 'Success')
            this.reasonModelRef.dismiss();
          }
        }, error => {
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = error?.error?.errors;
        })
      }
    }
  }
  removeSearch() {
    this.searchText = '';
    this.sort = 1;
    this.GetMainData();
  }

  sortByInvoice() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.GetMainData();
  }
  sortByCategory() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.GetMainData();
  }

  sortByIDate() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.GetMainData();
  }
  sortByEmployee() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.GetMainData();
  }

  sortByTotalAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.GetMainData();
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.GetMainData();
  }

  pageChange(page: number) {
    this.pageNo = page;
    this.checkedItems = [];
    this.GetMainData();
  }
  openModalExpense(item) {
    this.checkedItems = [];
    const modalRef = this.modalService.open(ExpenseDetailsComponent, {
      size: 'lg',
    });
    modalRef.componentInstance.expenseDetails = item;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      console.log('result', result);
      if (result) {
        this.GetMainData();
      }
    });
  }
}
