
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalImageComponent } from '../../../../cashier/petty-cash/modal-image/modal-image.component';
import { TreasuryService } from '../../../../treasury/petty-cash-treasury/services/treasury.service';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'app-expense-details',
  templateUrl: './expense-details.component.html',
  styleUrls: ['./expense-details.component.scss']
})
export class ExpenseDetailsComponent {
  @Input() expenseDetails: any;
  @Output() sendtoLoadData = new EventEmitter();
  message: string;
  messageError: string;
  type: string = 'Approved';
  msgError: string;
  RejectionReason: string;
  constructor(
    private treasuryService: TreasuryService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private router: Router
  ) { }

  ngOnInit(): void { }

  openMdalImageView() {
    const modalRef = this.modalService.open(ModalImageComponent);
    modalRef.componentInstance.imageUploadedView =
      this.expenseDetails?.files[0]?.attachmentPath;
  }
  chooseType(e) {
    const type = e.target.value;
    this.type = type;
  }
  ApproveExpense() {
    const formData = new FormData();
    formData.append('ExpenseRecordId', this.expenseDetails.expenseRecordId);
    return this.treasuryService
      .approverApproveExpense(formData)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.message = 'Approved successfully ';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.sendtoLoadData.emit(true);
          }, 3000);
        } else {
          console.log('error', response);
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          setTimeout(() => {
            this.messageError = '';
          }, 4000);
        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      });
  }
  requestReviseExpense() {
    if (!this.RejectionReason) {
      this.msgError = ' Reason is required';
      setTimeout(() => {
        this.msgError = '';
      }, 2000);
      return window.scroll();
    }
    const formData = new FormData();
    formData.append('ExpenseRecordId', this.expenseDetails.expenseRecordId);
    formData.append('Notes', this.RejectionReason);
    return this.treasuryService
      .requestReviseExpense(formData)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.message = 'Revised successfully ';
          this.RejectionReason = '';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.sendtoLoadData.emit(true);
          }, 3000);
        } else {
          console.log('error', response);
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          setTimeout(() => {
            this.messageError = '';
          }, 4000);
        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      });
  }
  RejectExpense() {
    if (!this.RejectionReason) {
      this.msgError = ' Reason is required';
      setTimeout(() => {
        this.msgError = '';
      }, 2000);
      return window.scroll();
    }
    const formData = new FormData();
    formData.append('ExpenseRecordId', this.expenseDetails.expenseRecordId);
    formData.append('RejectionReason', this.RejectionReason);
    return this.treasuryService
      .RejectExpense(formData)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.message = 'Rejected successfully ';
          this.RejectionReason = '';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.sendtoLoadData.emit(true);
          }, 3000);
        } else {
          console.log('error', response);
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          setTimeout(() => {
            this.messageError = '';
          }, 4000);
        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      });
  }

  submitExpenseAction() {
    if (this.type == 'Approved') {
      this.ApproveExpense();
    } else if (this.type == 'revise') {
      this.requestReviseExpense();
    } else {
      this.RejectExpense();
    }
  }
}
